.loading-container {
    position: relative;
    top: 0;
    left: 0;
    margin:auto;
}

.loading {
    position: absolute;
    z-index: 999;
    min-width: 100px;
    min-height:100%;
    width: 100%;
    height: 100%;

}

.loading .circle {
    position: absolute;
    display: inline-block;
    top: 2px;
    background: url(/omdb/static/media/loading-img.1854c4f1.png) no-repeat;
    background-size: contain;
}

.loading .circle-one {
    position: absolute;


    left: calc(47% - 50px);

    width: 50px;
    height: 50px;

    -webkit-animation: spin 5s infinite;

            animation: spin 5s infinite;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.loading .circle-two {
    left: calc((47% - 50px) + 47px);
    width: 40px;
    height: 40px;
    -webkit-animation: spin-aclockwise 3s infinite;
            animation: spin-aclockwise 3s infinite;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}


.loading .circle-three {
    left: calc((47% - 50px) + 82px);
    top: 19px;
    width: 35px;
    height: 35px;

    -webkit-animation: spin 3.5s infinite;

            animation: spin 3.5s infinite;
    -webkit-animation-duration: 3.5s;
            animation-duration: 3.5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes spin-aclockwise {
    from {
        -webkit-transform: rotate(-12deg);
                transform: rotate(-12deg);
    }
    to {
        -webkit-transform: rotate(-372deg);
                transform: rotate(-372deg);
    }
}

@keyframes spin-aclockwise {
    from {
        -webkit-transform: rotate(-12deg);
                transform: rotate(-12deg);
    }
    to {
        -webkit-transform: rotate(-372deg);
                transform: rotate(-372deg);
    }
}

